* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    #root {
        max-width: 630px;
        width: 630px;
        margin: auto;
        background: linear-gradient(0deg, #0062cc, #0abcce);
        box-shadow: 5px 5px 150px rgba(0, 0, 0, .15);
        border-radius: 2rem;
        padding: 2rem;
        height: 630px;
        font-family: "Montserrat";
        overflow: hidden;

        // .row {
        //     flex-wrap: nowrap !important;
        // }
        nav {
            flex-wrap: nowrap !important;
        }

        h2 {
            text-transform: uppercase;
            font-weight: 600;
        }

        .delo {
            color: #ff0099;
            font-family: monospace;
            font-size: 2.5rem;
            text-shadow: 2px 2px #fff;
        }

        .logo {
            img {
                height: 3rem;
                width: auto;
            }
        }
    }
}

body {
    width: 630px;
    height: 630px;
    margin: auto;
}